import DjGroup from '../../components/dj-group/DjGroup.vue';
import DjCellContent from '../pslinfo/DjCellContent.vue';
import { _Date as dataTime } from '../../common/common';
export default {
    components: {
        DjCellContent,
        DjGroup
    },
    data() {
        return {
            dataList: [
                {title: "", cont: "5000"},
                {title: "", cont: "200"},
                {title: "", cont: "500"}
            ],
            selectTime: dataTime.getFormatTime('YYYYMM'),
            originTime: dataTime.getFormatTime('YYYY-MM')
        };
    },
    methods: {
        selectSalaryTime() {
            var _this = this;
            this.$vux.datetime.show({
                cancelText: '',
                confirmText: '',
                format: 'YYYY-MM',
                value: _this.originTime,
                onConfirm(val) {
                    _this.selectTime = _this.getFormVal(val);
                    _this.originTime = val;
                },
                onShow() {

                },
                onHide() {

                }
            });
        },
        getFormVal(val) {
            let arr = val.split('-');
            return `${arr[0]}${arr[1]}`;
        }
    }
};



